@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);

:root {
    /* iOS insets */
    --safe-area-inset-top: 20px;
    --safe-area-inset-bottom: 0px;

    --page: #fff;
    --page_tr: rgba(255,255,255,0.8);
    --accent: #2E70EA;
    --header: rgba(255,255,255,1);
    --separator: rgba(0,0,0,0.1);
    --text: #222;
    --text_secondary: rgba(34, 34, 34, 0.7);
    --button-primary: var(--accent);
    --button-primary-fg: white;
    --button-secondary: rgba(246, 246, 246);
    --button-secondary-fg: var(--text);
}

.Placeholder {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 12px 0;
}
.Placeholder__icon {
    font-size: 48px;
    padding: 24px 0 12px;
    overflow: hidden;
}
.Placeholder__title {
    font-size: 22px;
}
.Placeholder__content {
    margin: 0 auto;
    padding: 0 0;
}

.Button {
    margin: 8px 0;
    background: #2E70EA;
    background: var(--button-primary);
    color: white;
    color: var(--button-primary-fg);
    border-bottom: 1px solid rgba(0,0,0,0.1);
    border-bottom: 1px solid var(--separator);
    border-radius: 8px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    transition-duration: .05s;
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.Button__secondary {
    margin: 8px 0;
    background: rgba(246, 246, 246);
    background: var(--button-secondary);
    color: #222;
    color: var(--button-secondary-fg);
    border-bottom: 1px solid rgba(0,0,0,0.1);
    border-bottom: 1px solid var(--separator);
    border-radius: 8px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    transition-duration: .05s;
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}
.Button__content {
    margin: 0 auto;
    padding-top: 2px;
}
.Button__before {
    padding-right: 8px;
}
.Button:active {
    opacity: 0.6;
    transition-duration: .05s;
}
.Button__secondary:active {
    opacity: 0.6;
    transition-duration: .05s;
}
.Button__m {
    padding: 8px 12px;
}
.Button__l {
    padding: 10px 18px;
}
.Button__xl {
    width: auto;
    padding: 12px 24px;
    text-align: center;
}
.Auth__in {
    margin-top: 115px;
}

.Auth__AuthBlock {
    position: relative;
    width: 290px;
    margin: 20px auto 0;
}

.Auth__AuthBlock > h3 {
    font-size: 20px;
    line-height: 28px;
    font-weight: 700;
    margin-bottom: 12px;
}

.Auth_AuthBlock_image {
    text-align: center;
}

.Auth__NumberInput {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.Auth__NumberInput > label {
    font-size: 14px;
    line-height: 20px;
    color: #8b8b8b;
    display: flex;
    align-self: flex-start;
    margin: 0 0 4px;
    cursor: pointer;
}

.Auth__NumberInput_input {
    flex-grow: 1;
}

.Auth__NumberInput_el {
    width: 260px;
    border: 1px solid #d5d5d5;
    border-radius: 8px;
    font-size: 16px;
    line-height: 22px;
    line-height: 1;
    color: #000;
    padding: 10px 14px 11px;
}
* {
  font-family: 'Roboto', sans-serif;
}

body {
  height: 100%;
}
