:root {
    /* iOS insets */
    --safe-area-inset-top: 20px;
    --safe-area-inset-bottom: 0px;

    --page: #fff;
    --page_tr: rgba(255,255,255,0.8);
    --accent: #2E70EA;
    --header: rgba(255,255,255,1);
    --separator: rgba(0,0,0,0.1);
    --text: #222;
    --text_secondary: rgba(34, 34, 34, 0.7);
    --button-primary: var(--accent);
    --button-primary-fg: white;
    --button-secondary: rgba(246, 246, 246);
    --button-secondary-fg: var(--text);
}

.Placeholder {
    display: flex;
    flex-direction: column;
    text-align: center;
    padding: 12px 0;
}
.Placeholder__icon {
    font-size: 48px;
    padding: 24px 0 12px;
    overflow: hidden;
}
.Placeholder__title {
    font-size: 22px;
}
.Placeholder__content {
    margin: 0 auto;
    padding: 0 0;
}

.Button {
    margin: 8px 0;
    background: var(--button-primary);
    color: var(--button-primary-fg);
    border-bottom: 1px solid var(--separator);
    border-radius: 8px;
    width: fit-content;
    transition-duration: .05s;
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
    user-select: none;
}
.Button__secondary {
    margin: 8px 0;
    background: var(--button-secondary);
    color: var(--button-secondary-fg);
    border-bottom: 1px solid var(--separator);
    border-radius: 8px;
    width: fit-content;
    transition-duration: .05s;
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
    user-select: none;
}
.Button__content {
    margin: 0 auto;
    padding-top: 2px;
}
.Button__before {
    padding-right: 8px;
}
.Button:active {
    opacity: 0.6;
    transition-duration: .05s;
}
.Button__secondary:active {
    opacity: 0.6;
    transition-duration: .05s;
}
.Button__m {
    padding: 8px 12px;
}
.Button__l {
    padding: 10px 18px;
}
.Button__xl {
    width: auto;
    padding: 12px 24px;
    text-align: center;
}