.Auth__in {
    margin-top: 115px;
}

.Auth__AuthBlock {
    position: relative;
    width: 290px;
    margin: 20px auto 0;
}

.Auth__AuthBlock > h3 {
    font-size: 20px;
    line-height: 28px;
    font-weight: 700;
    margin-bottom: 12px;
}

.Auth_AuthBlock_image {
    text-align: center;
}

.Auth__NumberInput {
    width: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
}

.Auth__NumberInput > label {
    font-size: 14px;
    line-height: 20px;
    color: #8b8b8b;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-item-align: start;
    align-self: flex-start;
    margin: 0 0 4px;
    cursor: pointer;
}

.Auth__NumberInput_input {
    -webkit-box-flex: 1;
    -ms-flex-positive: 1;
    flex-grow: 1;
}

.Auth__NumberInput_el {
    width: 260px;
    border: 1px solid #d5d5d5;
    border-radius: 8px;
    font-size: 16px;
    line-height: 22px;
    line-height: 1;
    color: #000;
    padding: 10px 14px 11px;
}